import { Component } from '@angular/core';
import { NavController, App, MenuController, AlertController, Events } from 'ionic-angular';
import { DashboardPage } from '../dashboard/dashboard';
import { LoginService } from './login.service';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})

export class LoginPage {

  public username: String = "";
  public password: String = "";

  constructor(
    private alertCtrl: AlertController,
    private menu: MenuController,
    // public navCtrl: NavController,
    protected app: App,
    public loginService: LoginService,
    public events: Events
  ) {

  }
  get navCtrl(): NavController {
    console.log(this.app.getRootNav());
    return this.app.getRootNav();
  }

  ionViewDidEnter() {
    this.menu.swipeEnable(false);
  }

  ionViewWillLeave() {
    this.menu.swipeEnable(true);
  }

  afterlogin() {

    var data = {
      "username": this.username,
      "password": this.password
    }
    // if(data.username !== "" && data.password !== "")
    this.loginService.login(data).subscribe((data) => {
      console.log("------>>> ", data)
      this.events.publish('user:created', data);
      sessionStorage.setItem('currentuser', JSON.stringify(data))
      sessionStorage.setItem('token', JSON.stringify(data.token))
      if (data.user.uuid !== "" || data.user.uuid !== null) {
        this.navCtrl.setRoot(DashboardPage);
      }
    },
      (error) => {
        console.log("--error-->>", error);
        // this.presentAlert('Wrong Credentials Entered..!');
      });
    // this.navCtrl.setRoot(DashboardPage);

  }

  public presentAlert(message: string): void {
    let alert = this.alertCtrl.create({
      title: 'Alert..!',
      message: message,
      enableBackdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'OK',
          handler: () => {
            this.username = "",
              this.password = ""
          }
        }
      ]
    });
    alert.present();
  }
}


