import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { OrderFilterComponent } from './orderFilter/orderFilterComponent';
import { OrderDetailsComponent } from './orderDetails/orderDetailsComponent';

import { OrderService } from './order.service'

@Component({
  selector: 'page-orderComponent',
  templateUrl: 'orderComponent.html',

})

export class OrderComponent implements OnInit {

  public allList: {};
  private searchTerm: any;
  private listOfOrdersToFilter: {};
  public location: any[] = [];
  public status: any[] = [];
  constructor(public navCtrl: NavController, private orderService: OrderService) {

  }

  searchQuery: string = '';
  items: string[];
  orderslist: any;

  ionViewDidLoad() {

  }

  ngOnInit(): void {
    this.add_orderslist();
  }

  filterOrder() {
    this.navCtrl.push(OrderFilterComponent,{
      location : this.location,
      status: this.status
    });
  }

  displayOrderDetails(order) {
    console.log("----> order details--> ", order);
    this.navCtrl.push(OrderDetailsComponent, {
      orderDetails: order
    });
  }

  add_orderslist() {

    this.orderService.getallOrders()
      .subscribe(data => {
        console.log('data--order----->', data);
        this.listOfOrdersToFilter = data;
        this.orderslist = data
        console.log("data length ---- ", data.length)
        for (let i = 0; i < data.length; i++) {
          console.log("entering into for loop --- ",this.location.indexOf(data[i].location))
          if (this.location.indexOf(data[i].location) > -1) {
          }
          else {
            this.location.push(data[i].location)
          }
          if (this.status.indexOf(data[i].status) > -1) {
          }
          else {
            this.status.push(data[i].status)
          }
        }
        console.log("list of location --- ", this.location);
        console.log("list of status  --- ", this.status);
        
      },
        error => {
          console.log('error------->', error);
        });
  }

  setFilteredItems() {

    // const filteredValue = this.dataService.filterItems(this.listOfTicketItems, this.searchTerm);
    const filteredValue = this.filterItems(this.listOfOrdersToFilter, this.searchTerm);
    console.log("searched ticket are --- ", filteredValue);
    if (filteredValue === false) {
      console.log("entering into filteredd valeus ")
      this.orderslist = this.listOfOrdersToFilter;
    }
    else {
      console.log("entering into else part")
      this.orderslist = filteredValue;

    }

  }

  filterItems(items, searchTerm) {
    if (searchTerm === '') {
      return false;
    }
    else {
      return items.filter((item) => {
        console.log("entering into -item ---- ", item)
        if (item.location.toLowerCase().search(searchTerm.toLowerCase()) > -1 ||
          item.po.toString().search(searchTerm) > -1 ||
          item.date.search(searchTerm) > -1 ||
          item.uuid.search(searchTerm) > -1 ||
          item.tickets.toString().search(searchTerm) > -1 ||
          item.status.toLowerCase().search(searchTerm.toLowerCase()) > -1) {
          return true;
        }
      });
    }
  }


  // getItems(ev: any) {
  //   this.orderslist = this.allOrderssToFilter

  //   let val = ev.target.value;

  //   if (val && val.trim() != '') {
  //     this.orderslist = Object(this.orderslist).filter((item) => {
  //       return (item.location.toLowerCase().search(val.toLowerCase()) > -1);
  //     })
  //   }
  // }
}



